<template>
    <div class="user">
        <el-table
        :data="userList"
        style="width: 100%">
            <el-table-column
                prop="openid"
                label="微信编号">
            </el-table-column>
            <el-table-column
                prop="id"
                label="用户编号">
            </el-table-column>
            <el-table-column
                prop="times"
                label="注册日期">
            </el-table-column>
            <el-table-column label="操作" v-if="roles == 'admin'">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[5,10,15,20]"
                    :page-size="pagesize"
                    :total="total"    
                    layout="total, sizes, prev, pager, next, jumper">    
            </el-pagination>
        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                userList:[],
                roles:"",
                currentPage:1, //初始页
                pagesize:20,    //    每页的数据
                total:3, // 订单列表总数量
            }
        },
        created() {
            this.PageUserList(); // 用户分页列表
            this.getUser(); // 获取用户
            this.roles = this.$store.state.userInfo.roles;
        },
        methods: {
            // 初始页currentPage、初始每页数据数pagesize和数据data
            handleSizeChange: function (size) {
                this.pagesize = size;
                this.PageUserList(); // 分页订单列表
                console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                this.PageUserList(); // 分页订单列表
                console.log(this.currentPage)  //点击第几页
            },
            PageUserList () {
                let that = this;
                let url = '/api'+this.$common.user_page;
                let data = {
                    offset:that.currentPage,
                    limit:that.pagesize
                }
                that.$http.post(url,data).then((res)=>{
                    console.log(res)
                    if ( res.code == '200' ) {
                        that.userList = res.data;
                        that.total = res.num;
                        setTimeout(function(){
                            that.loading = false;
                        },2000)
                    }
                }).catch(data=>{

                })
            },
            getUser () {
                let that = this;
                let url = '/api'+this.$common.user_all;
                this.$http.post(url).then((res)=>{
                    if ( res.code == '200' ) {
                        that.userList = res.data;
                    } else if( res.code == '404' ){
                        sessionStorage.clear();
                        this.$router.push({ path:'/login' });
                    }              
                }).catch(data=>{

                })
            },
            handleEdit () {

            },
            handleDelete () {
                
            }
        },
        mounted() {
            
        },
    }
</script>

<style lang="scss" scoped>
    .page {
        padding: 30px 0;
        float: right;
    }
</style>



